	module.exports = {"data":{"markdownRemark":{"html":"<p>早朝に、私は \"曽國府\"の第2部のいくつかの章を読んで、読書を終わらせなかった。私は昨日最初の部分を終えました。この本は首都で好きだと言われています。これは歴史的な小説であり、基本的な意味は、曽は苦しい時代にどのように育ったのか、苦しい時代の政治と軍の対比を描いている。中国の過去公務員の説明と公務員の権力闘争の記述も深遠です。それはただ対象が太平軍だから、太平軍を武器として使うのは手伝ってはいけません。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0081.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0081.md","frontmatter":{"title":"4月25日 月曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0081.md absPath of file >>> MarkdownRemark"}}

